<template>
  <!--  -->
  <div class="image-container">
    <!--  v-for  -->
    <div class="backg1" ref="backg1"></div>
    <div class="backg2" ref="backg2"></div>
    <img
      v-for="(image, index) in images"
      :key="index"
      :src="getImageUrl(image.imgUrl)"
      :alt="image.alt"
      :class="{ normal: index === activeIndex }"
      :style="getImageStyle(index)"
      @mouseenter="handleMouseEnter(index)"
      @mouseleave="handleMouseLeave"
      @click="clickImg(image)"
    />
  </div>
</template>

<script>
// 
import imgData from "@/assets/data/imgdata.json";

export default {
  data() {
    return {
      //  JSON 
      images: imgData,
      //  0
      activeIndex: 0,
    };
  },
  methods: {
    clickImg(image) {
      //  image  sessionStorage 
      sessionStorage.setItem("data", JSON.stringify(image));

      //  imgList 
      if (image && image.imgList && image.imgList.length > 0) {
        if (image.imgList.length == "3") {
          //  imgList  /imgIndex
          this.$router.push("/imgIndex");
        } else {
          this.$router.push("/imgIndex2");
        }
      } else {
        //  imgList  /indexText
        this.$router.push("/indexText");
      }
    },
    // 
    getImageUrl(imgPath) {
      //  Vue  require() log
      return require(`@/assets/${imgPath}`);
    },

    //  activeIndex 
    handleMouseEnter(index) {
      this.activeIndex = index;
    },

    // 
    handleMouseLeave() {
      // this.activeIndex = 0; // 
    },

    // 
    getImageStyle(index) {
      console.log();
      this.$nextTick(() => {
        if (this.$refs.backg1) {
          if (this.activeIndex <= 3) {
            this.$refs.backg1.style.display = "block";
          } else {
            this.$refs.backg1.style.display = "none";
          }
        }
        if (this.$refs.backg2) {
          if (this.activeIndex >= this.images.length - 4) {
            this.$refs.backg2.style.display = "block";
          } else {
            this.$refs.backg2.style.display = "none";
          }
        }
      });
      const distance = 6; // 
      const opacityBase = 1; // 
      const zIndexBase = 10; //  z-index 

      // 
      const offset = Math.abs(index - this.activeIndex);
      // 
      const opacity = opacityBase - offset * 0.2;
      //  z-index
      const zIndex = zIndexBase - offset;

      // 
      if (index < this.activeIndex) {
        const leftOffset = offset * distance;
        return {
          transformStyle: "preserve-3d",
          transform: `translateX(-${leftOffset}rem)  translateX(-90%) rotateY(80deg) translateY(-50%)`,
          // opacity: opacity,
          zIndex: zIndex, //  z-index 
          boxShadow:
            index !== this.activeIndex
              ? "0 4px 10px rgba(0, 0, 0, 0.2)"
              : "none", // 
        };
      } else if (index > this.activeIndex) {
        // 
        const rightOffset = offset * distance;
        return {
          transformStyle: "preserve-3d",
          transform: `translateX(${rightOffset}rem)  translateX(5%)  translateX(-15%) rotateY(-80deg) translateY(-50%)`,
          // opacity: opacity,
          zIndex: zIndex,
          boxShadow:
            index !== this.activeIndex
              ? "0 4px 10px rgba(0, 0, 0, 0.2)"
              : "none",
        };
      }

      //  1z-index 
      return {
        transformStyle: "preserve-3d",
        transform: "rotateY(0deg) translateX(-50%) scale(1) translateY(-50%) ", // 
        // opacity: opacityBase,
        zIndex: zIndexBase,
        width: "20%",
        height: "66%",
        boxShadow: "none", // 
      };
    },
  },
};
</script>

<style scoped>
/*  */
.image-container {
  transform-style: preserve-3d; /*  3D  */
  margin-top: 0;
  position: relative; /*  */
  width: 100%; /*  100% */
  height: 100%; /*  */
  overflow: hidden; /*  */
  background: #f2f2f2; /*  */
  perspective: 3400px; /*  1200px */
}

.backg1 {
  background: url("../assets/back/back.png");
  background-size: 100% 100%;

  width: 30%;
  height: 30%;
  position: absolute; /*  */
  top: 50%; /*  */
  left: 20%; /*  */
  transform: translateX(-50%) translateY(-50%); /*  */
}
.backg2 {
  background: url("../assets/back/back.png");
  background-size: 100% 100%;
  width: 30%;
  height: 30%;
  position: absolute; /*  */
  top: 50%; /*  */
  right: -10%; /*  */
  transform: translateX(-50%) translateY(-50%); /*  */
}

/*  */
img {
  width: 24%; /*  */
  height: 60%; /*  */
  position: absolute; /*  */
  top: 50%; /*  */
  left: 50%; /*  */
  transform-style: preserve-3d; /*  3D  */
  transform: translateX(-50%) translateY(-50%); /*  */
  transition: transform 0.5s ease, opacity 0.5s ease, box-shadow 0.5s ease; /*  */
}

/*  normal  z-index */
.normal {
  z-index: 10; /*  */
}

</style>


